	module.exports = {"data":{"allDataJson":{"edges":[{"node":{"bmjsiteHead":[{"id":"34866726","accountId":"5351042","webPropertyId":"UA-5351042-34","name":"bmjopen.bmj.com - Reporting","websiteUrl":"http://bmjopen.bmj.com"}],"bmjsiteData":[{"pagePath":"/content/5/2/e006784","pageTitle":"The combined perceptions of people with stroke and their carers regarding rehabilitation needs 1 year after stroke: a mixed methods study | BMJ Open","pageviews":"994"},{"pagePath":"/content/8/3/e018895","pageTitle":"Maternal caffeine intake during pregnancy and childhood growth and overweight: results from a large Norwegian prospective observational cohort study | BMJ Open","pageviews":"967"},{"pagePath":"/content/4/9/e005055","pageTitle":"The role of hospital managers in quality and patient safety: a systematic review | BMJ Open","pageviews":"857"},{"pagePath":"/content/8/2/e018846","pageTitle":"Assessing the nutrient intake of a low-carbohydrate, high-fat (LCHF) diet: a hypothetical case study design | BMJ Open","pageviews":"717"},{"pagePath":"/content/5/11/e008052.full","pageTitle":"Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share | BMJ Open","pageviews":"672"},{"pagePath":"/content/8/4/e019399","pageTitle":"Support for midlife anxiety diagnosis as an independent risk factor for dementia: a systematic review | BMJ Open","pageviews":"622"},{"pagePath":"/content/6/6/e010401","pageTitle":"Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review | BMJ Open","pageviews":"548"},{"pagePath":"/content/7/5/e015233","pageTitle":"Long-term antibiotics for prevention of recurrent urinary tract infection in older adults: systematic review and meta-analysis of randomised trials | BMJ Open","pageviews":"473"},{"pagePath":"/content/8/3/e019438","pageTitle":"Effect of pasta in the context of low-glycaemic index dietary patterns on body weight and markers of adiposity: a systematic review and meta-analysis of randomised controlled trials in adults | BMJ Open","pageviews":"440"},{"pagePath":"/content/7/10/e016089","pageTitle":"Do emotions related to alcohol consumption differ by alcohol type? An international cross-sectional survey of emotions associated with alcohol consumption and influence on drink choice in different settings | BMJ Open","pageviews":"397"}]}}]}},"pathContext":{"group":[{"node":{"id":"35508ed0-72ca-5d65-980f-45092521c37e","slug":"833","title":"BMJ Open&#8217;s fourth anniversary","excerpt":"<p>Today is the fourth anniversary of BMJ Open publishing its first papers. Since 23 February 2011 we have published over 3000 open access papers with our open peer review process. One hundred of these papers have accompanying datasets in the Dryad data repository. We are still one of very few medical journals integrated with Dryad [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/23/833/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 23, 2015","template":"","format":"standard"}},{"node":{"id":"12be2875-cc67-5bd6-8ee6-16e51b0f2082","slug":"thank-you-to-our-reviewers-2014","title":"Thank you to our reviewers &#8211; 2014","excerpt":"<p>After a very busy year at BMJ Open, in which over 1100 papers were published, we would like to say a big thank you to all of our reviewers who contributed in 2014. All that we achieved last year would not have been possible without the help of the many referees who gave thorough and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/18/thank-you-to-our-reviewers-2014/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 18, 2015","template":"","format":"standard"}},{"node":{"id":"70ce4fa4-c068-5acb-a566-45af142e9e6e","slug":"urban-cycling-physician-attire-and-the-impact-of-complaints-on-doctors-most-read-articles-in-january-2","title":"Urban cycling, physician attire and the impact of complaints on Doctors: Most Read Articles in January","excerpt":"<p>Several new papers made the January top 10 most read, including the cross-sectional survey by Bourne et al., on the impact of complaints on doctor&#8217;s health and psychological welfare. We also had a popular analysis of cycling injuries and the relationship with personal, trip, route and crash characteristics by Cripton et al. Petrelli et al.&#8216;s [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/12/urban-cycling-physician-attire-and-the-impact-of-complaints-on-doctors-most-read-articles-in-january-2/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 12, 2015","template":"","format":"standard"}},{"node":{"id":"58fd5535-b0b3-5e6b-98dc-a047e1ce392a","slug":"ucl-qualitative-health-research-symposium-2015","title":"UCL Qualitative Health Research Symposium 2015","excerpt":"<p>The UCL Division of Psychiatry’s Qualitative Researchers Working Group is working together with the UCL Department of Applied Health Research and the UCL Health Behaviour Research Centre on a one day symposium to discuss questions, and to generate constructive commentary on the contributions that qualitative inquiry can make to understandings of health, illness and care. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/10/ucl-qualitative-health-research-symposium-2015/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 10, 2015","template":"","format":"standard"}},{"node":{"id":"9c3c7e13-576b-5b7b-8b31-4ef606dd84dc","slug":"2014-a-year-in-review","title":"2014: A year in review","excerpt":"<p>2014 proved to be a successful and fruitful year for BMJ Open. We received over 2500 submissions, compared to just over 2000 in 2013. We published over 1100 papers. BMJ Open has always welcomed research article and protocol submissions, and in 2014, we expanded our scope to include cohort profiles. In mid year, Thomson Reuters [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/09/2014-a-year-in-review/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 09, 2015","template":"","format":"standard"}},{"node":{"id":"c463b8bc-65ec-5b8b-8a9c-b79ed46961ec","slug":"cumulative-daily-screen-time-linked-to-teen-sleep-problems","title":"Cumulative daily screen time linked to teen sleep problems","excerpt":"<p>Time to update recommendations on healthy use of electronic devices, say researchers The longer a teen spends on screen time throughout the day before going to bed, the worse quality sleep s/he is likely to have, suggests a large study published in BMJ Open. It’s time to update the recommendations on the healthy use of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/02/cumulative-daily-screen-time-linked-to-teen-sleep-problems/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 02, 2015","template":"","format":"standard"}},{"node":{"id":"4b7947f1-6b0f-5579-b502-69482fe45d44","slug":"sharp-and-sustained-rise-in-suicides-in-greece-linked-to-austerity-measures","title":"Sharp and sustained rise in suicides in Greece linked to austerity measures","excerpt":"<p>Give greater weight to mental health fall-out of future policies, urge researchers Suicides in Greece reached a 30 year all-time high in 2012, with a sustained upward trend starting in June 2011, the month that the government introduced further austerity measures to help pay down the country’s debts, reveals a 30 year study, published in [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/02/02/sharp-and-sustained-rise-in-suicides-in-greece-linked-to-austerity-measures/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 02, 2015","template":"","format":"standard"}},{"node":{"id":"c07cb347-8e94-5490-8618-01f1f565384b","slug":"falls-in-blood-pressure-and-cholesterol-have-saved-20000-lives-in-england","title":"Falls in blood pressure and cholesterol have saved 20,000+ lives in England","excerpt":"<p>Impact of statins greatest among most affluent but drugs only accounted for 14% of total fall in deaths Falls in blood pressure and total cholesterol staved off more than 20,000 deaths from coronary heart disease in England between 2000 and 2007, shows a mathematical analysis published in BMJ Open. The impact of statins was greatest [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/01/22/falls-in-blood-pressure-and-cholesterol-have-saved-20000-lives-in-england/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 22, 2015","template":"","format":"standard"}},{"node":{"id":"496ec805-d217-5673-b4d6-9603a4e18e23","slug":"uk-doctors-facing-complaints-dogged-by-severe-depression-and-suicidal-thoughts","title":"UK doctors facing complaints dogged by severe depression and suicidal thoughts","excerpt":"<p>UK doctors subject to complaints procedures are at significant risk of becoming severely depressed and suicidal, reveals research published in the online journal BMJ Open. Those referred to the UK professional regulator, the General Medical Council (GMC), seem to be most at risk of mental ill health, the findings suggest. The researchers base their findings [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/01/15/uk-doctors-facing-complaints-dogged-by-severe-depression-and-suicidal-thoughts/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 15, 2015","template":"","format":"standard"}},{"node":{"id":"9e97b607-c484-510b-b3d0-e57db4685286","slug":"few-uk-family-doctors-seem-to-be-treating-obesityoverweight-appropriately","title":"Few UK family doctors seem to be treating obesity/overweight appropriately","excerpt":"<p>Patient health records suggest limited evidence of weight management in primary care Few UK family doctors seem to be treating overweight/obesity appropriately, with some not treating it all, suggests an analysis of patient records published in BMJ Open. The researchers scrutinised the anonymised health records of more than 90,000 obese and overweight adults, whose data [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/01/12/few-uk-family-doctors-seem-to-be-treating-obesityoverweight-appropriately/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 12, 2015","template":"","format":"standard"}}],"pathPrefix":"","first":false,"last":false,"index":14,"pageCount":27,"additionalContext":{}}}

